@import url("https://fonts.googleapis.com/css2?family=Comforter&family=Dancing+Script&family=Fredoka+One&family=Orbitron&family=Poppins&display=swap");

main {
  background-image: url("./assets/images/bg1.jpg");

  background-repeat: no-repeat;
  background-size: contain;
  overflow-y: auto;
  height: 100vh;
  background-color: #20202a;
}
.MuiSvgIcon-root {
  color: #fff;
}
.ScrollbarsCustom-Track {
  border-radius: 0px !important;
}
.ScrollbarsCustom-TrackY {
  top: 0px !important;
  background-color: rgba(25, 25, 35, 0.906) !important;
  height: 100% !important;
}
.App {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #20202a;
}
.container {
  max-width: 1350px;
}

@media (max-width: 600px) {
  .container {
    max-width: 365px;
  }
}
